
.testimonial{
    height: 70vh;
    display: flex;
    z-index: 5;
}


.carousel-item{
    margin:auto;
    width: 80vh;
    height: fit-content;
    background-color: #f7f8fa;
    border: 1px;
    border-radius: 20px;
    box-shadow: 5px 5px #A9A9A9;
}

.carousel-item{
    padding: 2% 3%;
    display: flex;
    justify-content: space-around;
    gap: 5vw;
}

.item-left{
    width: 40%;
    margin: auto;
}

.item-right{
    width: 60%;
}


.item-left img{
    width: 65%;
    border: 2px solid black;
    border-radius: 50%;
}

.item-header h1{
    font-size: 1.8em;
    color: #1363DF;
    margin-bottom: -5%;
    /* font-size: max(1.6vw,1.6vh); */
}

.item-header h4{
    border-bottom:2px solid rgb(145, 143, 143);
    margin-right: 20px;
    padding-bottom: 5px;
    /* font-size: max(1.3vw,1.3vh); */
}

.item-right p{
    font-size: 1.2em;
    /* font-size: max(1.2vw,1.2vh); */
}

.carousel-indicators [data-bs-target] {
    background-color: #37b0a1;
    border-radius: 50%;
    height: 7px;
    width: 7px;
  }

