.cbp_tmtimeline {
	margin: 30px 0 0 0;
	padding: 0;
	list-style: none;
	position: relative;
  z-index: -1;
} 
.timeline-main,
.timeline-container > header {
	/* width: %; */
	display: flex;
	/* max-width: 69em; */
	/* margin: 0 auto; */
	padding: 0 1.875em 3.125em 4.5em;
}
.cbp_tmtimeline{
	width: 50%;
}
.aboutus-descp{
	width: 50%;
	margin-right: 35px;
	padding: 20px 20px 20px 50px;
	text-align: justify;
}
/* The line */
.cbp_tmtimeline:before {
	content: '';
	position: absolute;
	top: 20px;
	bottom: 0;
	width: 4px;
	height: 420px;
	background:#1B65A7;
	left: 32.5%;
	/* margin-left: -10px; */
}

/* The date/time */
.cbp_tmtimeline > li {
	position: relative;
}

.cbp_tmtimeline > li .cbp_tmtime {
	display: block;
	width: 5%;
	padding-right: 100px;
  padding-top: 25px;
	position: absolute;
	/* padding-right: 50px; */
}

.cbp_tmtimeline > li .cbp_tmtime span {
	display: block;
	text-align: right;
	padding-left:70px;
}

/* .cbp_tmtimeline > li .cbp_tmtime span:first-child {
	font-size: 0.9em;
	color: #bdd0db;
} */

.cbp_tmtimeline > li .cbp_tmtime span:last-child {
  margin-top: 15px;
  font-weight: bold;
	font-size: 1.5em;
	color: #1B65A7;
}

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmtime span:last-child {
  margin-top: 15px;
  font-weight: bold;
	color: #1B65A7;
}

/* Right content */
.cbp_tmtimeline > li .cbp_tmlabel {
	margin: 0 0 30px 47%;
	background:  #1B65A7;
	color: #fff;
	padding: 0.5em;
	font-size: 1.2em;
	font-weight: 300;
	line-height: 1.4;
	position: relative;
	border-radius: 30px;
}

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel {
	background: #F2F4F5;
}

.cbp_tmtimeline > li .cbp_tmlabel h2 { 
	margin-top: 0px;
	padding: 0 0 10px 0;
	border-bottom: 1px solid rgba(255,255,255,0.4);
}
.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel p{
	text-align: center;
  line-height: 20px;
  color: #000000;
  font-weight: bold;
  font-size: 25px;
}
.cbp_tmtimeline > li .cbp_tmlabel p{
  text-align: center;
  line-height: 20px;
  color:  #F2F4F5;
  font-weight: bold;
  font-size: 25px;
}
/* The triangle */
.cbp_tmtimeline > li .cbp_tmlabel:after {
	right: 100%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
 	margin-top: 28px;
	border-right-color: #1B65A7 ;
	border-width: 10px;
	top: 10px;
}

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel:after {
	border-right-color:#EFF9F8;
}

/* The icons */

#about-icon{
	margin-top: 20px;
}
