.about-home {
    width: 100%;
    overflow-x: hidden;
  }
  
  .heading-text  {
    text-align: center;
    padding: 20px 20px;
  }
  .heading-text h1{
    color: #fff;
    font-size: 60px;
  }
  .heading-text p{
    padding: 0% 25% 0% 25%;
    color: #fff                           ;
  }
  .heading-main{
    /* background-image: url('../images/aboutus.jpg'); */
    background-size:100% 300px;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 300px;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 25px;
    background-repeat: no-repeat;
  }
  #about-head {
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 48px;
    line-height: 56px;
  }
.about-content{
    margin-left: 5%;
    margin-right: 5%;
	margin-top: 40px;
}
.timeline-container{
	margin-top: 100px;
	/* display: flex; */
	width: 100%; 
}
.cbp_tmtimeline {
	margin: 30px 0 0 0;
	padding: 0;
	list-style: none;
	position: relative;
  z-index: -1;
} 
.timeline-main,
.timeline-container > header {
	/* width: %; */
	display: flex;
	/* max-width: 69em; */
	/* margin: 0 auto; */
	padding: 0 1.875em 3.125em 4.5em;
}
.cbp_tmtimeline{
	width: 50%;
}
.aboutus-descp{
	width: 50%;
	margin-right: 35px;
	padding: 20px 20px 20px 50px;
	text-align: justify;
}
/* The line */
.cbp_tmtimeline:before {
	content: '';
	position: absolute;
	top: 20px;
	bottom: 0;
	width: 4px;
	height: 420px;
	background:#1B65A7;
	left: 32.5%;
	/* margin-left: -10px; */
}

/* The date/time */
.cbp_tmtimeline > li {
	position: relative;
}

.cbp_tmtimeline > li .cbp_tmtime {
	display: block;
	width: 5%;
	padding-right: 100px;
  padding-top: 25px;
	position: absolute;
	/* padding-right: 50px; */
}

.cbp_tmtimeline > li .cbp_tmtime span {
	display: block;
	text-align: right;
	padding-left:70px;
}

/* .cbp_tmtimeline > li .cbp_tmtime span:first-child {
	font-size: 0.9em;
	color: #bdd0db;
} */

.cbp_tmtimeline > li .cbp_tmtime span:last-child {
  margin-top: 15px;
  font-weight: bold;
	font-size: 1.5em;
	color: #1B65A7;
}

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmtime span:last-child {
  margin-top: 15px;
  font-weight: bold;
	color: #1B65A7;
}

/* Right content */
.cbp_tmtimeline > li .cbp_tmlabel {
	margin: 0 0 30px 47%;
	background:  #1B65A7;
	color: #fff;
	padding: 0.5em;
	font-size: 1.2em;
	font-weight: 300;
	line-height: 1.4;
	position: relative;
	border-radius: 30px;
}

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel {
	background: #F2F4F5;
}

.cbp_tmtimeline > li .cbp_tmlabel h2 { 
	margin-top: 0px;
	padding: 0 0 10px 0;
	border-bottom: 1px solid rgba(255,255,255,0.4);
}
.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel p{
	text-align: center;
  line-height: 20px;
  color: #000000;
  font-weight: bold;
  font-size: 25px;
}
.cbp_tmtimeline > li .cbp_tmlabel p{
  text-align: center;
  line-height: 20px;
  color:  #F2F4F5;
  font-weight: bold;
  font-size: 25px;
}
/* The triangle */
.cbp_tmtimeline > li .cbp_tmlabel:after {
	right: 100%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
 	margin-top: 28px;
	border-right-color: #1B65A7 ;
	border-width: 10px;
	top: 10px;
}

.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel:after {
	border-right-color:#EFF9F8;
}

/* The icons */

#about-icon{
	margin-top: 20px;
}
.cbp_tmtimeline > li .cbp_tmicon {
	width: 60px;
	height: 60px;
	font-family: 'ecoico';
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	font-size: 1.4em;
	line-height: 40px;
	-webkit-font-smoothing: antialiased;
	position: absolute;
	color: #fff;
	background: #fff;
  background-position: center;
  background-repeat: no-repeat;
	border-radius: 50%;
  border: 4px solid #1B65A7;
	text-align: center;
	left: 31.3%;
	top: 0;
	margin: 0 0 0 -25px;
}
.bank-icon{
  width: 50px;
  height: 50px;
  margin-top: 18px;
  margin-right: 15px;
}
/* .cbp_tmicon-phone:before {
	content: "\e000";
} */
.about-img{
	padding:10px ;
	height: 35px;
	width: 35px;
}
.cbp_tmicon-screen:before {
	content: "\e001";
}

.cbp_tmicon-mail:before {
	content: "\e002";
}

.cbp_tmicon-earth:before {
	content: "\e003";
}

@media screen and (max-width: 65.375em) {

	.cbp_tmtimeline > li .cbp_tmtime span:last-child {
		font-size: 1.5em;
	}
}
@media screen and (max-width: 1230px){
	.cbp_tmtimeline > li .cbp_tmtime span{
		padding-left: 50px;
	}
}
@media screen and (max-width: 1100px){
	.cbp_tmtimeline > li .cbp_tmtime span{
		padding-left: 30px;
	}

}

@media screen and (max-width: 920px) {
	.cbp_tmtimeline:before {
		display: none;
	}
	.about-img{
		height: 26px;
		width: 26px;
	}
	.cbp_tmtimeline > li .cbp_tmtime {
		width: 100%;
		position: relative;
		padding: 0 0 20px 0;
	}

	.cbp_tmtimeline > li .cbp_tmtime span {
		text-align: left;
		padding-left: 20px;
	}

	.cbp_tmtimeline > li .cbp_tmlabel {
		margin: 0 0 30px 0;
		padding: 1em;
		font-weight: 400;
		font-size: 95%;
	}

	.cbp_tmtimeline > li .cbp_tmlabel:after {
		right: auto;
		left: 25px;
		border-right-color: transparent;
		border-bottom-color: #1B65A7;
		top: -48px;
	}
	.heading-main{
		margin-left: 0px;
	}
	.cbp_tmtimeline > li:nth-child(odd) .cbp_tmlabel:after {
    /* left: 3px; */
		border-right-color: transparent;
		border-bottom-color: #EFF9F8;
    margin-top: 45px;
	}

	.cbp_tmtimeline > li .cbp_tmicon {
    background-size: 30px;
    width: 50px;
    height: 42px;
		position: relative;
		float: right;
		left: auto;
		margin: -55px 5px 0 0px;
	}	
	#about-icon{
		margin-top: -55px;
	}
	.timeline-main{
		display: block;
		padding-left: 2em;
	}
	.cbp_tmtimeline{
		width: 100%;
	}
	.aboutus-descp{
		width: 100%;
		padding: 0px;
	}
}
